@mixin inputarea {
  &_inputbox{
    align-items: center;
    width: -webkit-fill-available;
    &_head{
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      &_target{
        width: 30%;
      }
      &_explication{
        width: 70%;
        margin: 0px 5px;
      }
    }
    &_buttom{
      &_example{
        width: -webkit-fill-available; 
        margin-right: 5px;
      }
    }
  }
}
.App {
  padding: 30px 10%;
  font-weight: 600;
  &_header{
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    &_no{
      flex: auto;
    }
    &_setting{
      font-size: 1.5rem;
      & > *{
        width: 30px;
        margin-left: 10px;
      }
    }
  }
  &_input{
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    margin-bottom: 30px;
    @include inputarea;
  }
  &_listitem{
    &_hidden{
      font-size: 1.5rem;
          text-align: center;
      & >*{
        margin: 0 20px;
      }
    }
  }
  &_modify{
    position: fixed;
    z-index: 10;
    top: 150px;
    left: 0px;
    background: white;
    box-shadow: 5px 5px 15px -3px rgba(0, 0, 0 , 0.7);
    border-radius: 10px;
    padding: 10px;
    margin: 10px;
    font-size: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: -webkit-fill-available;
    @include inputarea;
    &_submit{
      & > * {
        margin: 0 15px;
      }
    }
    textarea{
      height: 100px;
    }
  }
}

textarea{
  border-radius: 5px;
  border: 1px solid black;
  font-size: 1rem;
  font-weight: 600;
  height: 40px;
}

.fa-pencil{
  margin-right: 10px;
}

.totopbutton{
  border: none;
  background-color: transparent;
  font-size: 3rem;
  position: fixed;
  top: 93%;
  right: 2%;
  transform: translateY(-50%);
  animation: btn-popup 1s;
}